<style lang="scss" scoped>
.star {
  display: flex;
  flex-wrap: wrap;
  // align-items: stretch;
  padding-bottom: 0;
  padding-right: 0;
}
.star-card {
  box-sizing: border-box;
  width: 25%;
  flex: none;
  padding-right: 15px;
  padding-bottom: 15px;
}
.star-card-content {
  position: relative;
  border-radius: 5px;
  padding: 20px;
  border: 1px solid #eee;
  background-color: #fafafa;
}
.star-line {
  display: flex;
}
.star-line + .star-line {
  margin-top: 10px;
}
.star-line-left {
  flex: none;
  width: 60px;
  text-align: right;
  line-height: 20px;
  font-size: 14px;
  color: #909399;
  margin: 0;
  margin-right: 10px;
}
.star-line-right {
  flex: auto;
  line-height: 20px;
  font-size: 14px;
  margin: 0;
  color: #606266;
}
.star-avatar {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 64px;
  height: 64px;
  font-size: 30px;
  line-height: 62px;
  text-align: center;
  border: 1px solid #eee;
  background-size: cover;
  background-repeat: none;
  background-position: center;
  color: #606266;
}
