  height: 64px;
  overflow: scroll;
}
.no-wrap {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>

<template>
  <div>
    <div class="container">
      <el-button type="primary" @click="startAdd">创建学员</el-button>
    </div>

    <div class="container star">
      <div class="star-card" v-for="item of list" :key="item.id">
        <div class="star-card-content">
          <div class="star-line">
            <p class="star-line-left">姓名</p>
            <p class="star-line-right no-wrap" style="padding-right: 60px;">{{ item.name }}</p>
          </div>
          <div class="star-line">
            <p class="star-line-left">专业</p>
            <p class="star-line-right no-wrap" style="padding-right: 60px;">{{ item.discipline }}</p>
          </div>
          <div class="star-line">
            <p class="star-line-left">简介</p>
            <p class="star-line-right introduction">{{ item.introduction }}</p>
          </div>
          <p class="star-operation">
            <el-button type="text" @click="() => startDelete(item)">删除</el-button>
            <el-button type="text" @click="() => startEdit(item)">编辑</el-button>
          </p>
          <div
            class="star-avatar"
            :style="{
              backgroundImage: `url(${item.avatar})`
            }"
          >
            {{ item.avatar ? '' : item.name ? item.name[0] : '' }}
          </div>
        </div>
      </div>
    </div>
    <el-dialog :title="current.id ? '修改明星学员信息' : '添加明星学员'" :visible.sync="editDialogVisible" width="60%">
      <el-form :model="current" :rules="rules" label-width="80px" label-position="right" ref="editForm">
        <el-form-item label="头像">
          <upload-file :limit="1" accept="image/*" v-model="current.avatar"></upload-file>
        </el-form-item>
        <el-form-item label="姓名" prop="name">
          <el-input v-model="current.name"></el-input>
        </el-form-item>
        <el-form-item label="专业" prop="discipline">
          <el-input v-model="current.discipline"></el-input>
        </el-form-item>
        <el-form-item label="简介">
          <el-input type="textarea" v-model="current.introduction"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmEdit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
/* eslint-disable */
import { M } from '../../service/grpc.js';
import { rpc } from '../../service/index.js';
import { getToken } from '../../service/helper.js';
import UploadFile from '../../components/uploadFile/index.vue';

export default {
  components: { UploadFile },
  data() {
    return {
      token: getToken(),
      list: [],
      current: {
        name: '',
        discipline: '',
        introduction: '',
        avatar: ''
      },
      rules: {
        name: [{ required: true, message: '请填写学生姓名' }],
        discipline: [{ required: true, message: '请填写学生专业' }]
      },
      editDialogVisible: false
    };
  },
  created() {
    rpc(
      'StoreStudentService.GetStudentList',
      M('StudentListQueryRequest', {
        token: M('Token', { token: this.token }),
        count: 100
      })
    ).then(res => {
      this.list = res.studentlistList;
    });
  },
  methods: {
    startAdd() {
      this.current = {
        name: '',
        discipline: '',
        introduction: '',
        avatar: ''
      };
      this.editDialogVisible = true;
    },
    startEdit(item) {
      this.current = JSON.parse(JSON.stringify(item));
      this.editDialogVisible = true;
    },
    startDelete(item) {
      this.$confirm(`确定要删除“${item.name}”吗？`, '删除明星学员', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.remove(item);
        })
        .catch(e => {});
    },
    remove(item) {
      rpc(
        'StoreStudentService.DeleteStudent',
        M('IDWithTokenRequest', {
          token: M('Token', { token: this.token }),
          id: item.id
        })
      ).then(() => {
        this.list.splice(this.list.findIndex(v => v.id == item.id), 1);
        this.$message({
          message: '删除成功！',
          type: 'success'
        });
      });
    },
    confirmEdit() {
      this.$refs.editForm.validate(res => {
        if (res) {
          if (this.current.id) {
            rpc(
              'StoreStudentService.UpdateStudent',
              M('StudentWithTokenRequest', {
                token: M('Token', { token: this.token }),
                student: M('Student', this.current)
              })
            ).then(res => {
              this.list.splice(this.list.findIndex(v => v.id == res.id), 1, res);
              this.editDialogVisible = false;
              this.$message({
                message: '修改成功！',
                type: 'success'
              });
            });
            return;
          }
          rpc(
            'StoreStudentService.CreateStudent',
            M('StudentWithTokenRequest', {
              token: M('Token', { token: this.token }),
              student: M('Student', this.current)
            })
          ).then(res => {
            this.list.splice(0, 0, res);
            this.editDialogVisible = false;
            this.$message({
              message: '添加成功！',
              type: 'success'
            });
          });
        }
      });
    }
  }
};
</script>
